import { ReactTyped } from "react-typed";

function Hero({ data }) {

    const { name, fields = [], links = {} } = data;
    const { twitter, facebook, instagram, linkedin } = links;

    return (
        <section id="hero" className="hero section light-background">

            <img src="assets/img/hero-bg.jpg" alt="" />

            <div className="container" data-aos="zoom-out">
                <div className="row justify-content-center">
                    <div className="col-lg-9">
                        <h2>{name}</h2>
                        <p>
                            I'm&nbsp;
                            <ReactTyped
                                strings={fields}
                                typeSpeed={100}
                                backSpeed={50}
                                backDelay={2000}
                                loop
                                className="typed"
                            >
                                <span className="typed">{fields[0] ?? ''}</span>
                            </ReactTyped>
                            <span className="typed-cursor typed-cursor--blink" aria-hidden="true"></span>
                        </p>
                        <div className="social-links">
                            <a href={twitter}><i className="bi bi-twitter-x"></i></a>
                            <a href={facebook}><i className="bi bi-facebook"></i></a>
                            <a href={instagram}><i className="bi bi-instagram"></i></a>
                            <a href={linkedin}><i className="bi bi-linkedin"></i></a>
                        </div>
                    </div>
                </div>
            </div>

        </section>
    );
}

export default Hero;