import { useState } from "react";

function Header() {

    const [ isShowHeader, setIsShowHeader ] = useState(false);
    const [ isHeaderList, setIsHeaderList ] = useState(true);
    const [ isHeaderX, setIsHeaderX ] = useState(false);

    function handleToggleBtnClick() {
        setIsShowHeader(!isShowHeader);
        setIsHeaderList(!isHeaderList);
        setIsHeaderX(!isHeaderX);
    }

    function handleNavMenuClick() {
        if (isShowHeader) {
            handleToggleBtnClick();
        }
    }

    return (
        <header id="header" className={'header d-flex flex-column justify-content-center' + (isShowHeader ? ' header-show' : '')}>
            <i className={'header-toggle d-xl-none bi' + (isHeaderList ? ' bi-list' : '') + (isHeaderX ? ' bi-x' : '')} onClick={handleToggleBtnClick}></i>

            <nav id="navmenu" className="navmenu">
                <ul>
                    <li><a href="#hero" onClick={handleNavMenuClick} className="active"><i className="bi bi-house navicon"></i><span>Home</span></a></li>
                    <li><a href="#about" onClick={handleNavMenuClick}><i className="bi bi-person navicon"></i><span>About</span></a></li>
                    <li><a href="#resume" onClick={handleNavMenuClick}><i className="bi bi-file-earmark-text navicon"></i><span>Resume</span></a></li>
                    <li><a href="#portfolio" onClick={handleNavMenuClick}><i className="bi bi-images navicon"></i><span>Portfolio</span></a></li>
                    <li><a href="#services" onClick={handleNavMenuClick}><i className="bi bi-hdd-stack navicon"></i><span>Services</span></a></li>
                    <li><a href="#contact" onClick={handleNavMenuClick}><i className="bi bi-envelope navicon"></i><span>Contact</span></a></li>
                </ul>
            </nav>
        </header>
    )
}

export default Header;