function Footer({ data }) {

    const { copyright, name, tagline, links = {} } = data;
    const { twitter, facebook, instagram, skype, linkedin } = links;

    return (
        <footer id="footer" className="footer position-relative light-background">
            <div className="container">
                <h3 className="sitename">{name}</h3>
                <p>{tagline}</p>
                <div className="social-links d-flex justify-content-center">
                    <a href={twitter}><i className="bi bi-twitter-x"></i></a>
                    <a href={facebook}><i className="bi bi-facebook"></i></a>
                    <a href={instagram}><i className="bi bi-instagram"></i></a>
                    <a href={skype}><i className="bi bi-skype"></i></a>
                    <a href={linkedin}><i className="bi bi-linkedin"></i></a>
                </div>
                <div className="container">
                    <div className="copyright">
                        <span>Copyright</span> <strong className="px-1 sitename">{copyright}</strong> <span>All Rights Reserved</span>
                    </div>
                    <div className="credits">
                        Designed by <a href={window.location.href}>{name}</a>
                    </div>
                </div>
            </div>
        </footer>
    );
}

export default Footer;