import { useState } from "react";
import { Waypoint } from "react-waypoint";
import SkillProgressBar from "../components/SkillProgressBar";

function Skills() {

    const [showProgress, setShowProgress] = useState(false);

    function handleSkillsAnimationEnter() {
        setShowProgress(true);
    }

    return (
        <section id="skills" className="skills section">

            <div className="container section-title" data-aos="fade-up">
                <h2>Skills</h2>
                <p>Necessitatibus eius consequatur ex aliquid fuga eum quidem sint consectetur velit</p>
            </div>

            <div className="container" data-aos="fade-up" data-aos-delay="100">

                <Waypoint onEnter={handleSkillsAnimationEnter} topOffset={'20%'}>

                    <div className="row skills-content skills-animation">

                        <div className="col-lg-6">

                            <div className="progress">
                                <span className="skill"><span>HTML</span> <i className="val">100%</i></span>
                                <div className="progress-bar-wrap">
                                    <SkillProgressBar valueNow='100' showProgress={showProgress} />
                                </div>
                            </div>

                            <div className="progress">
                                <span className="skill"><span>CSS</span> <i className="val">90%</i></span>
                                <div className="progress-bar-wrap">
                                    <SkillProgressBar valueNow='90' showProgress={showProgress} />
                                </div>
                            </div>

                            <div className="progress">
                                <span className="skill"><span>JavaScript</span> <i className="val">75%</i></span>
                                <div className="progress-bar-wrap">
                                    <SkillProgressBar valueNow='75' showProgress={showProgress} />
                                </div>
                            </div>

                        </div>

                        <div className="col-lg-6">

                            <div className="progress">
                                <span className="skill"><span>PHP</span> <i className="val">80%</i></span>
                                <div className="progress-bar-wrap">
                                    <SkillProgressBar valueNow='80' showProgress={showProgress} />
                                </div>
                            </div>

                            <div className="progress">
                                <span className="skill"><span>WordPress/CMS</span> <i className="val">90%</i></span>
                                <div className="progress-bar-wrap">
                                    <SkillProgressBar valueNow='90' showProgress={showProgress} />
                                </div>
                            </div>

                            <div className="progress">
                                <span className="skill"><span>Photoshop</span> <i className="val">55%</i></span>
                                <div className="progress-bar-wrap">
                                    <SkillProgressBar valueNow='55' showProgress={showProgress} />
                                </div>
                            </div>

                        </div>

                    </div>

                </Waypoint>

            </div>

        </section>
    );
}

export default Skills;